$font-size: 18px;
$font-family: 'arial';
$global-prefixes: webkit moz ms o;
$breakpoints: (
  screen1: 1024px,
  screen2: 1280px,
  screen3: 1366px,
  screen4: 1400px,
  screen5: 1600px,
  screen6: 1680px,
  screen7: 1920px,
  screen8: 2560px,
);

@mixin prefixer($prop, $val, $prefixes: null) {
  @if $prefixes == null {
    @each $prefix in $global-prefixes {
      -#{ $prefix }-#{ $prop }: $val;
    }
  } @else {
    @each $prefix in $prefixes {
      -#{ $prefix }-#{ $prop }: $val;
    }
  }

  #{ $prop }: $val;
}

@mixin keyframes($name) {
  @-webkit-keyframes #{$name} {
    @content;
  }

  @-moz-keyframes #{$name} {
    @content;
  }

  @keyframes #{$name} {
    @content;
  }
}

@mixin media($width) {
  @media only screen and (max-width: $width) {
    @content;
  }
}

@mixin media-between($width, $maxwidth) {
  @media (min-width: $width) and (max-width: $maxwidth) {
    @content;
  }
}

@mixin respond-to($breakpoint) {
  @if map-has-key($breakpoints, $breakpoint) {
    @media (min-width: #{map-get($breakpoints, $breakpoint)}) {
      @content;
    }
  } @else {
    @warn "Unfortunately, no value could be retrieved from `#{$breakpoint}`. "
          + "Please make sure it is defined in `$breakpoints` map.";
  }
}

@mixin flex($content: center, $direction: row, $align: stretch) {
  display: flex;
  justify-content: $content;
  flex-direction: $direction;
  align-items: $align;
}
