.form-group{
    position: relative;
}
.form-label {
    position: absolute;
    left: 10px;
    top: 10px;
    color: #999;
    background-color: #fff;
    z-index: 10;
    font-size: 14px;
    transition: transform 200ms ease-out, font-size 200ms ease-out, left 200ms ease-out;
  }
  .focused .form-label {
    transform: translateY(-180%);
    left:0;
   // font-size: .75em;
  }