@import '../../../index.scss';

$userInputWidth: 290px;
$userInputHeight: 287px;

.loginContainer {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  background-size: cover;

  .login-box {
    width: 316px;
    height: 441px;
    border-radius: 10px;
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.16);
    background-color: rgba(238, 27, 49, 0.55);
    position: relative;
    margin: auto;
    top: 55%;
    float: right;
    right: 12%;
    transform: translateY(-50%);

    .logo-container {
      width: 100%;
      height: 139px;
      position: relative;

      .logo-container-inner {
        width: 120px;
        height: 65px;
        margin: auto;
        position: relative;
        top: 31px;

        .logo-img {
          width: 100%;
          height: 100%;
          object-fit: contain;
        }
      }
    }

    .login-box-inner {
      padding: 0 13px 15px 13px;

      .user-area {
        width: $userInputWidth;
        height: $userInputHeight;
        border-radius: 10px;
        background-color: #ffffff;
        padding-left: 28px;
        padding-right: 27px;
        padding-top: 49px;

        .user-input-area {
          position: relative;
        }

        .user-name {
          width: 235px;
          height: 38px;

          input {
            width: 100%;
            height: 100%;
            border: none;
            border: 0.5px solid #ccc;
            border-radius: 6px;
            background-color: #ffff;
          }
        }

        .help-block {
          position: absolute;
          bottom: -20px;
          font-size: 12px;
          right: 0px;
          color: $textColor;
        }

        .password-area {
          padding-top: 35px;
          position: relative;
        }

        .submit-area {
          padding-top: 30px;
          position: relative;
        }

        .submit-button {
          width: 235px;
          height: 38px;
          border-radius: 20px;
          border: transparent;
          margin: 0;
          background-image: linear-gradient(to bottom, #f8154d, #e6201b);
          // background-color:  #841916;
          font-family: Roboto;
          font-size: 12px;
          font-weight: 500;
          font-stretch: normal;
          font-style: normal;
          line-height: 1.33;
          letter-spacing: normal;
          text-align: center;
          color: #ffffff;
          transition: all 0.5s ease-in;
        }

        .forgot-pass {
          text-align: center;
          padding-top: 15px;

          .forgot-pass-link {
            text-decoration: none;
            font-family: Roboto;
            font-size: 14px;
            font-weight: 500;
            font-stretch: normal;
            font-style: normal;
            line-height: 1.14;
            letter-spacing: normal;
            text-align: left;
            color: $textColor;
            outline: none;
            background: transparent;
            border: none;
            cursor: pointer;
          }
        }
      }
    }
  }
}
